<template>
  <div class="container">
    <div class="col-sm-10">
      <h1>Задачи</h1>
       <confirmation
      
      :status = status
      :showConfirmation = showConfirmation
      :message="confirmationMessage"
      v-if="showConfirmation">
    </confirmation>
      <button type="button" id="task-add" class="btn btn-success btn-sm align-left d-block"  v-b-modal.modal @click="addTodo()">Добавить задачу</button>
      <p>Всего задач: {{ all_task()}} выполнено: {{complite_task()}} </p>
      <table class="table table-dark table-stripped table-hover">
        <thead class="thead-light">
          <tr>
            <th>Uid</th>
            <th>Описание</th>
            <th>Выполнена?</th>
            <th></th>
          </tr>
        </thead>

        <tbody>
          <tr v-for="(todo,index) in todos" :key="index">
            <td class="todo-uid">{{ todo.uid }}</td>
            <td>{{ todo.description }}</td>
            <td>
              <span v-if="todo.is_completed">Выполнено</span>
              <span v-else>Не выполнено</span>
            </td>
            <td>
              <div class="btn-group" role="group">
                <button type="button" class="btn btn-secondary btn-sm" v-b-modal.modal
        @click="updateTodo(todo)">Обновить</button>
                &nbsp;
                <button type="button" class="btn btn-danger btn-sm" @click="deleteTodo(todo)">X</button>
              </div>
            </td>
          </tr>
        </tbody>

      </table>



<b-modal ref="Modal"
         id="modal"
         :title=Modeltitle
         hide-footer>
  <b-form @submit="onSubmit_new" @reset="onReset" class="w-100">
   <b-form-group id="form-update-description-group"
                label="Описание:"
                label-for="form-update-description-input">
    <b-form-input id="form-update-description-input"
                  type="text"
                  v-model="Form.description"
                  required>
    </b-form-input>
  </b-form-group>
  <b-form-group id="form-complete-group">
    <b-form-checkbox-group v-model="Form.is_completed" id="form-checks">
      <b-form-checkbox value="true">Задача выполнена?</b-form-checkbox>
    </b-form-checkbox-group>
    </b-form-group>
    <b-button type="submit" variant="primary">{{ btnText }}</b-button>
    <b-button type="reset" variant="danger">Сброс</b-button>
  </b-form>
</b-modal>


    </div>

  </div>
</template>

<style>
button#task-add {
  margin-top: 20px;
  margin-bottom: 20px;
}
h1, td {
  text-align: left;
}
.todo-uid {
  text-align: right;
}
</style>


<script>

import Confirmation from './Confirmation.vue';



export default {
  name: 'Todo',
  data() {

    return {
      
      uid: 0,
      todos: [],
      addTodoForm: {
        description: '',
        
        is_completed: [],
      },
     
       Form: { 
        uid: 0,
        description: '',
        is_completed: [],
      },
      btnText: '',
      Submit: '',
      Modeltitle: '',
      confirmationMessage: '',
      showConfirmation: false,
      status: '',
    };
  },

  methods: {
    all_task(){
      return  JSON.parse(localStorage.getItem('todos')).length;
    },
    complite_task(){
      
      return JSON.parse(localStorage.getItem('todos')).filter((n) => {return n.is_completed === true}).length;
      

    },

    
    getTodos() {
      
      this.todos = JSON.parse(localStorage.getItem('todos'));
      
    },
    resetForm() {

      this.Form.description = ''; 
      
      this.Form.is_completed = [];
    },
  
    onSubmit_new(event) {
      event.preventDefault();
      this.$refs.Modal.hide();
      const requestData = {
        description: this.Form.description,

        is_completed: this.Form.is_completed[0],

      };
      
      if (requestData.is_completed){

        requestData.is_completed=true;
      }else{

        requestData.is_completed=false;
      }
      var uid = 1;
         
      const data = JSON.parse(localStorage.getItem('todos'));

      if (this.btnText == 'Добавить'){

         
          if (data){
          // получение максимального uid
          var maxUid = data.reduce((max, item) => item.uid > max ? item.uid : max, 0);
          
          data.push({'uid':maxUid+1, 'description':requestData.description,'is_completed':requestData.is_completed})
          const parsed = JSON.stringify(data);
          localStorage.setItem('todos', parsed)
        }else{

          const parsed = JSON.stringify([{'uid':uid, 'description':requestData.description,'is_completed':requestData.is_completed}]);
          localStorage.setItem('todos', parsed)
        }
          
         
          this.getTodos();
           
          this.resetForm();
      }
      else{
        const task = ({'uid':this.Form.uid, 'description':requestData.description,'is_completed':requestData.is_completed});
        
        const data = JSON.parse(localStorage.getItem('todos'));
        
        const data_new = data.map(o=>{if (o.uid === this.Form.uid) {return task;} return o;} );
        
        localStorage.setItem('todos', JSON.stringify(data_new))


          this.getTodos();
          this.confirmationMessage = `Задача "${requestData.description}" обновлена`;
          this.showConfirmation = true;
          this.status = 'success';
        
      }
    },

   
    onReset(event) {
      event.preventDefault();
      this.$refs.Modal.hide();
      this.resetForm();
    },
    addTodo() { 

      this.btnText = 'Добавить';
      this.Submit = 'onSubmit';
      this.Modeltitle = 'Добавить задачу';
      
      
    },
    updateTodo(todo) { 
      this.btnText = 'Обновить';
      this.Form = todo;

      this.Modeltitle = 'Обновить';
     
      
    },
  


    deleteTodo(todo) {
      let data = JSON.parse(localStorage.getItem('todos'));
      let task = data.find(item => item.uid == todo.uid);
      data = data.filter((n) => {return n != task});
      
      
      localStorage.setItem('todos', JSON.stringify(data))

    todo.uid;
        this.getTodos();
        this.confirmationMessage = `Задача "${todo.description}" удалена`;
        this.showConfirmation = true;
        this.status = 'success';
   
}
  },
  components: {
    confirmation: Confirmation,
  },
  created() {
    this.getTodos();
  },
 
};
</script>